import React, { useMemo, useState } from "react";
import "./Table.css";
import {
  useReactTable,
  getCoreRowModel,
  flexRender,
} from "@tanstack/react-table";
import { unparse } from "papaparse";

const ReusableTable = ({ columns, data, editable = false }) => {
  const [tableData, setTableData] = useState(data);

  const handleEditCell = (rowIndex, columnId, value) => {
    const updatedData = [...tableData];
    updatedData[rowIndex][columnId] = value;
    setTableData(updatedData);
  };

  // Enhance columns for editable functionality if enabled
  const enhancedColumns = useMemo(() => {
    if (!editable) return columns;

    return columns.map((column) => ({
      ...column,
      cell: column.editable
        ? ({ row, getValue }) => (
            <input
              value={getValue()}
              onChange={(e) =>
                handleEditCell(row.index, column.accessorKey, e.target.value)
              }
            />
          )
        : column.cell, // Retain existing cell rendering if not editable
    }));
  }, [columns, editable]);

  const table = useReactTable({
    data: tableData,
    columns: enhancedColumns,
    getCoreRowModel: getCoreRowModel(),
  });

  const handleExportCSV = () => {
    const csv = unparse(tableData); // Convert JSON to CSV
    const blob = new Blob([csv], { type: "text/csv;charset=utf-8;" });
    const link = document.createElement("a");
    link.href = URL.createObjectURL(blob);
    link.setAttribute("download", "table-data.csv");
    link.click();
  };

  return (
    <>
    <div className="toolkit">
    <button
        onClick={handleExportCSV}
        className="btn btn-outline-primary btn-lg border-bottom-10px"
      >
        Export to CSV
      </button>
      </div>
      <table>
        <thead>
          {table.getHeaderGroups().map((headerGroup) => (
            <tr key={headerGroup.id}>
              {headerGroup.headers.map((header) => (
                <th key={header.id}>
                  {flexRender(
                    header.column.columnDef.header,
                    header.getContext()
                  )}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody>
          {table.getRowModel().rows.map((row) => (
            <tr key={row.id}>
              {row.getVisibleCells().map((cell) => (
                <td key={cell.id}>
                  {flexRender(cell.column.columnDef.cell, cell.getContext())}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </>
  );
};

export default ReusableTable;
