import React, { useState } from "react";
import "./Card.css";
import "bootstrap/dist/css/bootstrap.css";
import { Button, Card, CardBody } from "react-bootstrap";
import List from "../List/List";
// import { IoMdArrowDropdown } from "react-icons/io";

const CardComponent = ({data}) => {
  const [isColumn, setIsColumn] = useState(true);

  const toggleFlexDirection = () => {
    setIsColumn(!isColumn);
  };

  return (
    <Card className="d-flex flex-row pt-4 mb-5">
      <div className="pb-3">
        <Card.Img
          src={
            "https://oi000186152-us1-space.3dexperience.3ds.com/enovia/snresources/images/icons/large/I_VPMNavProduct108x144.png"
          }
        />
      </div>
      <CardBody className="p-0">
      <List data={data} isColumn={true} />
      </CardBody>
      <Button
        style={{ height: "30px" }}
        className="me-2 p-0"
        onClick={toggleFlexDirection}
      >
        {/* <IoMdArrowDropdown /> */}
      </Button>
    </Card>
  );
};

export default CardComponent;
